import React, {useState, useRef} from 'react';
import {Button, Chip, Grid, MenuItem, TextField} from '@mui/material';
import tours from './data/TourData';
import TourComponent from './TourComponents/TourComponent';
import './TourList.scss';
import destinations from "./data/DestinationData";
import DestinationDetail from "./DestinationalDetail/DestinationDetail";

const TourList = () => {
    const [selectedLocations, setSelectedLocations] = useState([]);
    const [filteredTours, setFilteredTours] = useState([]);
    const [isSearched, setIsSearched] = useState(false);
    const resultsRef = useRef(null); // Ref for the results container

    const handleLocationChange = (event) => {
        setSelectedLocations(event.target.value);
    };

    const handleSearch = () => {
        if (selectedLocations.length > 0) {
            // Filter tours based on selected locations
            const filtered = tours.filter(tour =>
                selectedLocations.every(location => tour.locations.includes(location))
            );
            setFilteredTours(filtered);
            setIsSearched(true);
        } else {
            // Clear filtered tours and show all tours if no locations selected
            setFilteredTours([]);
            setIsSearched(true);
        }

        // Scroll to results
        if (resultsRef.current) {
            resultsRef.current.scrollIntoView({behavior: 'smooth', block: 'start'});
            window.scrollTo({top: 0, behavior: 'smooth'}); // Scroll to top of the page
        }
    };

    return (
        <div className="container">
            <div className="searchBar">
                <TextField
                    autoFocus
                    style={{
                        margin: '0',
                        borderRadius: '10px',
                        backgroundColor: 'white',
                        fontSize: '16px'
                    }}
                    select
                    label="Select Locations"
                    value={selectedLocations}
                    onChange={handleLocationChange}
                    SelectProps={{
                        multiple: true,
                        renderValue: (selected) => (
                            <div style={{display: 'flex', flexWrap: 'wrap'}}>
                                {selected.map(value => (
                                    <Chip key={value} label={value} style={{margin: '2px'}}/>
                                ))}
                            </div>
                        ),
                    }}
                    variant="outlined"
                    fullWidth
                    margin="normal"
                    InputLabelProps={{shrink: selectedLocations.length > 0}}
                >
                    {['Thp b', 'Lng ngh chiu ci: L Hi, Vnh Ngc', 'Lng ngh lm gm L Cm: L Cm, Vnh Ngc', 'Lng chi Xm Bng', 'Farm Din ng', 'Thnh c Din Khnh', 'Vin Hi Dng Hc'].map((location) => (
                        <MenuItem key={location} value={location}>
                            {location}
                        </MenuItem>
                    ))}
                </TextField>
                <Button variant="contained" color="primary" onClick={handleSearch}
                        style={{marginTop: '0', height: '56px', borderRadius: '10px'}}>
                    Search
                </Button>
            </div>
            <div className="content">
                {isSearched && (
                    <div ref={resultsRef}>
                        <h6 style={{color: 'black', fontSize: '35px', margin: '0'}}>
                            Search Results:
                        </h6>
                        {filteredTours.length > 0 && (
                            <Grid container spacing={3} style={{marginTop: '20px', marginBottom: '50px'}}>
                                {filteredTours.map(tour => (
                                    <Grid item xs={12} sm={6} md={4} key={tour.id}>
                                        <TourComponent tour={tour}/>
                                    </Grid>
                                ))}
                            </Grid>
                        )}
                        {filteredTours.length === 0 && (
                            <h6 style={{color: 'black', fontSize: '35px', margin: '25px 0 0 0'}}>
                                Not Found
                            </h6>
                        )}
                    </div>
                )}
            </div>
            <h6 style={{margin: '20px 0', color: '#1a1a1a', fontSize: '35px', textAlign: 'center'}}>
                Famous Location
                <div style={{fontSize: '25px', color: '#9d9595'}}>The most popular choice for visitors from
                    Vietnam</div>
            </h6>
            {destinations.map((destination, index) => (
                <DestinationDetail
                    key={destination.id}
                    destination={destination}
                    reverseOrder={index % 2 !== 0} // B sung props reverseOrder
                />
            ))}
        </div>
    );
};

export default TourList;
