import React from 'react';
import {useParams} from 'react-router-dom';
import {Card, CardContent, Typography, Grid, Button} from '@mui/material';
import tours from '../data/TourData';

const TourDetail = () => {
    const {id} = useParams();
    const tour = tours.find(tour => tour.id === parseInt(id));

    if (!tour) {
        return <Typography variant="h5">Tour not found</Typography>;
    }

    return (
        <>
            <Card style={{margin: '20px', padding: '20px'}}>
                <CardContent>
                    {/* Top Row: Tour Title */}
                    <Typography variant="h4" gutterBottom>{tour.title}</Typography>
                    {/* Second Row: Image and Price/Booking Button */}
                    <Grid container spacing={3}>
                        <Grid item xs={12} md={8}>
                            <img src={tour.image} alt={tour.title} style={{width: '100%', height: 'auto'}}/>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Typography variant="h5" color="textSecondary" gutterBottom>
                                {`Price: ${tour.price}`}
                            </Typography>
                            <Button variant="contained" color="primary" fullWidth>
                                Book Now
                            </Button>
                        </Grid>
                    </Grid>
                    {/* Third Row: Tour Details */}
                    <Grid container spacing={3} style={{marginTop: '20px'}}>
                        <Grid item xs={12} md={8}>
                            <Typography variant="body1" style={{whiteSpace: 'pre-line'}}>
                                {tour.details}
                            </Typography>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            {/* Additional information or empty */}
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </>
    );
};

export default TourDetail;
